"use client";
import React from "react";
import dynamic from "next/dynamic";

const Template1 = ({ data }) => {
  return (
    <div>
      {data?.sections?.map((item, index) => {
        const SectionName = item.section; // Example: "Header"
        const showhide = item.enabled;    // true / false

        // Dynamically load section component
        const Component = dynamic(
          () => import(`@/components/tempcomponent/${SectionName}.tsx`),
          { ssr: false }
        );

        // üëâ If section is disabled, skip rendering
        if (!showhide) return null;

        return (
          <Component
            key={index}
            data={item.fields}
            styles={data.styles}
          />
        );
      })}
    </div>
  );
};

export default Template1;
